export default {
	data() {
		return {
			current: "",
			currentIndex:"",
		}
	},
	props:{
		compyList:Array,
	},
	methods: {
		radioChange(evt) {
			for (let i = 0; i < this.compyList.length; i++) {
				if (this.compyList[i].compId === evt.target.value) {
					this.current = this.compyList[i].compId;
					this.currentIndex = i;
					break;
				}
			}
		},
		sureClick(){
			this.setGlobalUser(this.compyList[this.currentIndex]);
			uni.navigateBack();
		}
	}
}
